.Banner {
  text-align: center;
}

.Banner-logo {
  -webkit-animation: Banner-logo-spin infinite 20s linear;
          animation: Banner-logo-spin infinite 20s linear;
  height: 80px;
}

.Banner-header {
  background-color: #222;
  height: 80px;
  padding: 10px;
  color: white;
}

.Banner-title {
  font-size: 1.5em;
}

.Banner-intro {
  font-size: large;
}

@-webkit-keyframes Banner-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes Banner-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



